@import "vars";

.section {
  padding: 100px calc(50% - 470px);
  position: relative;
  background: white;

  &:nth-child(even) {
    background-color: #eee;
  }

  &__heading {
    left: 0;
    right: 0;
    top: -0.5em;
    margin: auto;
  }

  &__content {
    width: 100%;
  }
}

@media screen and (max-width: $small-screen-breakpoint) {
  .section {
    padding: 50px 20px;

    &__title {
      margin-bottom: 15px;
    }
  }
}
